import React, { useEffect, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction, designationAction, positionActions } from '../../../core/resource/actions'
import PositionTable from './PositionTable'
import { pagination } from '../../../utils/Constants.config'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import CreateUpdatePosition from './CreateUpdatePosition'

const Position = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation();

    const {
        addPositionPending,
        addPositionRes,
        addPositionError,

        getAllPositionPending,
        getAllPositionRes,
        getAllPositionError,

        updatePositionPending,
        updatePositionRes,
        updatePositionError,

        deletePositionPending,
        deletePositionRes,
        deletePositionError,
    } = useSelector(state => state.position)
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)
    const {
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentPending,
        getDesignationsByDepartmentError
    } = useSelector(state => state.designation)

    const [isOpenCreateUpdatePosition, setIsOpenCreateUpdatePosition] = useState(false)
    const [selectedPositionData, setSelectedPositionData] = useState('')
    const [allDepartments, setAllDepartments] = useState([])
    const [allDesignations, setAllDesignations] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [allPositions, setAllPositions] = useState({})
    const [isShowDeletePositionModal, setIsShowDeletePositionModal] = useState(false)
    const [deletePositionId, setDeletePositionId] = useState('')


    useEffect(() => {
        getAllDepartments()
    }, [])

    /* Create Update Position */ 
    const toggleCreateUpdatePositionModal = (data) => {
        if(!data){
            setSelectedPositionData('')
        }else{
            setSelectedPositionData(data)
        }
        setIsOpenCreateUpdatePosition(!isOpenCreateUpdatePosition)
    }

    const handleCreateUpdatePosition = (params) => {
        if (selectedPositionData) {
            params['_id'] = selectedPositionData?._id || selectedPositionData?.id
            dispatch(positionActions.updatePositionRequest(params));
        }
        else {
            dispatch(positionActions.addPositionRequest(params));
        }
    }

    useEffect(() => {
        if (!addPositionPending) {
            if (addPositionError) {
                dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_ADD_POSITION_STORE"))
                return;
            }
            if (addPositionRes) {
                if (addPositionRes?.code === 200) {
                    dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_ADD_POSITION_STORE"))
                    getAllPositions()
                    setIsOpenCreateUpdatePosition(!isOpenCreateUpdatePosition)
                }
            }
        }
    }, [
        addPositionRes,
        addPositionError,
        addPositionPending
    ]);

    useEffect(() => {
        if (!updatePositionPending) {
            if (updatePositionError) {
                dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_UPDATE_POSITION_STORE"))
                return;
            }
            if (updatePositionRes) {
                if (updatePositionRes?.code === 200) {
                    dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_UPDATE_POSITION_STORE"))
                    getAllPositions()
                    setIsOpenCreateUpdatePosition(!isOpenCreateUpdatePosition)
                }
            }
        }
    }, [
        updatePositionRes,
        updatePositionError,
        updatePositionPending
    ]);

    /* Get All Departments */
    const getAllDepartments = () => {
        const departmentResposnse = getDepartmentListRes?.data
        if (!departmentResposnse?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(departmentResposnse)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    const handleDesignationByDepartment = (id) => {
        const params = {
            department_id: id
        }
        dispatch(designationAction.getDesignationsByDepartmentRequest(params));
    }

    useEffect(() => {
        if (!getDesignationsByDepartmentPending) {
            if (getDesignationsByDepartmentError) {
                return;
            }
            if (getDesignationsByDepartmentRes) {
                if (getDesignationsByDepartmentRes?.code === 200) {
                    const designationResponse = getDesignationsByDepartmentRes?.data
                    setAllDesignations(designationResponse)
                }
            }
        }
    }, [
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentError,
        getDesignationsByDepartmentPending
    ]);
    useEffect(() => {
        getAllPositions()
    }, [currentPage])

    /* Get All Positions Data */
    const getAllPositions = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(positionActions.getAllPositionRequest(params));
    }

    useEffect(() => {
        if (!getAllPositionPending) {
            if (getAllPositionError) {
                return;
            }
            if (getAllPositionRes) {
                if (getAllPositionRes?.code === 200) {
                    const getAllPositionData = getAllPositionRes?.data
                    setAllPositions(getAllPositionData)
                    
                }
            }
        }
    }, [
        getAllPositionRes,
        getAllPositionError,
        getAllPositionPending
    ]);


    /*  Delete Position   */
    const handlePositionDeleteModal = (id) => {
        if(id){
            setDeletePositionId(id)
        }
        setIsShowDeletePositionModal(!isShowDeletePositionModal)
    }

    const handleDeletePosition = (type) => {
        if (type) {
            const params = {
                _id: deletePositionId
            }
           
            dispatch(positionActions?.deletePositionRequest(params))

        } else {
            handlePositionDeleteModal()
        }

    }

    useEffect(() => {
        if (!deletePositionPending) {
            if (deletePositionError) {
                dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_DELETE_POSITION_STORE"))
                return;
            }
            if (deletePositionRes) {
                if (deletePositionRes?.code === 200) {
                    handlePositionDeleteModal()
                    if(currentPage >1 && allPositions?.data?.length===1){
                        setCurrentPage((pre=> pre -= 1 ))
                    }else{
                        getAllPositions()
                    }
                    dispatch(positionActions.handleClearPositionStore("HANDLE_CLEAR_DELETE_POSITION_STORE"))

                }
            }
        }
    }, [
        deletePositionRes,
        deletePositionError,
        deletePositionPending
    ]);

    return (
        <>
            <div className="d-flex  justify-content-end ">
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() =>toggleCreateUpdatePositionModal()}
                    text={t("btns.createNewPosition")}
                />
            </div>
            <CreateUpdatePosition
                openModel={isOpenCreateUpdatePosition}
                handleOpenClose={toggleCreateUpdatePositionModal}
                handleCreateUpdatePosition={handleCreateUpdatePosition}
                selectedPositionData={selectedPositionData}
                allDepartments={allDepartments}
                handleDesignationByDepartment={handleDesignationByDepartment}
                allDesignations={allDesignations}
            />
            <PositionTable
                toggleCreateUpdatePositionModal={toggleCreateUpdatePositionModal}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                allPositions={allPositions}
                handlePositionDeleteModal={handlePositionDeleteModal}
                isLoading={getAllPositionPending}
                
            />

            {isShowDeletePositionModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeletePosition}
                    isShow={isShowDeletePositionModal}
                    desc={t("alerts.deletePopup.desc").replace('$1',"Position")}
                    btnRightLoading={deletePositionPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Position
