import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { createShortName } from '../../../utils/common'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import {  addUpdatePositionSchema } from '../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../translation'

const CreateUpdatePosition = (props) => {
    const { addPositionPending, updatePositionPending } = useSelector(state => state?.position)
    const {t}=useCustomTranslation()
    const {
        selectedPositionData,
        openModel,
        handleOpenClose,
        allDepartments,
        handleDesignationByDepartment,
        allDesignations,
        handleCreateUpdatePosition
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdatePositionFormData, setCreateUpdatePositionFormData] = useState({
        department: null,
        designation: null,
        name: '',
        short_name: '',
        is_active: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };
        if (fieldName === "department") {
            handleDesignationByDepartment(value?.value)
            setCreateUpdatePositionFormData(prevState => ({
                ...prevState,
                ["designation"]: null,
            }));
        }
        setCreateUpdatePositionFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdatePosition = async () => {
        const validationResponse = await validateSchema(addUpdatePositionSchema, createUpdatePositionFormData);
        if (validationResponse.isValid) {
            const {
                department,
                designation,
                name,
                short_name,
                is_active
            } = createUpdatePositionFormData
            const params = {
                name,
                short_name,
                is_active,
                ["department_id"]: department?.value,
                ["designation_id"]: designation?.value,
            }
            setErrors({})
            handleCreateUpdatePosition(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(()=>{
        if(selectedPositionData){
            setCreateUpdatePositionFormData({
                department: {value:selectedPositionData?.department_id,label:selectedPositionData?.department_name},
                designation: {value:selectedPositionData?.designation_id,label:selectedPositionData?.designation_name},
                name: selectedPositionData?.name,
                short_name: selectedPositionData?.short_name,
                is_active: selectedPositionData?.is_active
            })
        }
    },[selectedPositionData])

    const resetForm = () => {
        setCreateUpdatePositionFormData({
            department: null,
            designation: null,
            name: '',
            short_name: '',
            is_active: true
        })
        setErrors({})
    }



    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                    {selectedPositionData ? t("headings.updatePosition"): t("headings.createPosition")} 
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
           

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.selectDepartment")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDepartments}
                                        placeholder={t("placeholders.selectDepartment")}
                                        value={createUpdatePositionFormData?.department || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "department")}
                                        name="department"
                                    />
                                    {errors?.department && (
                                        <CustomError text={errors.department} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.selectDesignation")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDesignations}
                                        placeholder={t("placeholders.selectDesignation")}
                                        value={createUpdatePositionFormData?.designation || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "designation")}
                                        name="designation"
                                    />
                                    {errors?.designation && (
                                        <CustomError text={errors.designation} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.positionName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.positionName")}
                                        value={createUpdatePositionFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdatePositionFormData?.short_name}
                                        name="short_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                     <Label  text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        // label={"Active"}
                                        checked={createUpdatePositionFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedPositionData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdatePosition()}
                            isLoading={selectedPositionData ? updatePositionPending : addPositionPending}
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateUpdatePosition
