import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'

const PositionTable = (props) => {
    const {t}=useCustomTranslation()
    const {
        allPositions,
        isLoading,
        toggleCreateUpdatePositionModal,
        handlePositionDeleteModal,
        currentPage,
        setCurrentPage
    } = props
    const [positionList, setPositionList] = useState(allPositions)


    const handleUpdatePosition = (data) => {
        toggleCreateUpdatePositionModal(data)
    }

    useEffect(() => {
        setPositionList(allPositions)
    }, [allPositions])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index+1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="actionGroup d-flex gap-2">
                <CustomButton
                    text={t("btns.update")}
                    classNames="btn btn-primary"
                    handleClick={() => {
                        handleUpdatePosition(item);
                    }}
                />
                {item?.emp_count===0&&<CustomButton
                    text={t("btns.delete")}
                    handleClick={() => handlePositionDeleteModal(item._id)}
                    classNames="btn btn-danger"
                />}
            </div>
        )
    }), [toggleCreateUpdatePositionModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'department_name', label: 'Departments' },
        { key: 'designation_name', label: 'Designations' },
        { key: 'name', label: 'Position Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'emp_count', label: 'Total Employees' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];
    return (
        <>
            <CustomTable
                data={positionList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={positionList?.totalPages}
                tableName={"Position"}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default PositionTable
